<!-- 课程详情 -->
<template>
	<div class="container">
		<div class="course-title flex-between">
			<div class="flex">
				<div>课程预约</div>
				<div>&nbsp;> 课程详情</div>
			</div>
			<i class="iconfont icon-del" @click="detailDel"></i>
		</div>
		<div class="box">
			<div>
				<div class="course-color">
					<div class="course-name">{{form.course.courseName}}</div>
					<div>
						<div>主讲老师:</div>
						<div>{{form.course.userName}}</div>
					</div>
					<div>
						<div>课程代码:</div>
						<div>
							<p>{{form.course.courseCode}}</p>
							<p style="margin-left: 15px;">{{form.course.status == 9010?'未开始':form.course.status == 9010?'进行中':'已结束'}}</p>
						</div>
					</div>
				</div>
				<div style="margin-top: 156px;">
					<div>教材:</div>
					<div>{{form.course.materialCodeName}}</div>
				</div>
				<div>
					<div>时间:</div>
					<div>{{form.course.startTime}} - {{form.course.endTime}}</div>
					<div style="margin-left: 15px;">第{{form.course.courseSection}}节</div>
				</div>
				<div>
					<div>教室:</div>
					<div>{{form.course.roomName}}</div>
				</div>
				<div>
					<div>简介:</div>
					<div>{{form.course.introduction}}</div>
				</div>
				<div>
					<div>课件:</div>
					<div style="flex-direction: column;">
						<div v-for="item in form.attachment">
							<div>{{item.attachmentName}}</div>
							<a :href="item.attachmentUrl">
								<i class="iconfont iconxiazai"></i>
							</a>
						</div>
					</div>
				</div>
				<div>
					<div>
						<div>高级信息</div>
						<i></i>
					</div>
				</div>
				<div v-if="form.course.courseOrder != 8001">
					<div>开课方式:</div>
					<div>{{form.course.courseWay==8012?'互动方式':'直播方式'}}</div>
				</div>
				<div v-if="form.course.courseOrder == 8002&&form.course.courseWay == 8012">
					<div>录制方式:</div>
					<div>{{form.course.recordWay==8020?'本地录制':'云录制'}}</div>
				</div>
				<div v-if="!(form.course.courseOrder == 8002&&form.course.courseWay==8013)">
					<div>录像状态:</div>
					<div>{{form.course.status>=9013?'已上传':'未上传'}}</div>
				</div>
				<div v-if="form.course.courseOrder == 8002&&form.course.courseWay == 8012">
					<div>邀请教室:</div>
					<div v-for="item in form.yqRooms">{{item}}</div>
				</div>
				<div v-if="form.course.courseOrder == 8002&&form.course.courseWay==8013">
					<div>教室全景:</div>
					<div>{{form.live.roomFullUrl}}</div>
				</div>
				<div v-if="form.course.courseOrder == 8002&&form.course.courseWay==8013">
					<div>板书画面:</div>
					<div>{{form.live.blackBoardUrl}}</div>
				</div>
				<div v-if="form.course.courseOrder == 8002&&form.course.courseWay==8013">
					<div>ppt画面:</div>
					<div>{{form.live.pptUrl}}</div>
				</div>
				<div v-if="form.course.courseOrder == 8002&&form.course.courseWay==8013">
					<div>观看地址:</div>
					<div>{{form.live.lookUrl}}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
    data(){
        return {
			id:'387596164566097920',
			form:{}
        }
    },
	created() {
		this.detailSearch()
	},
	methods:{
		detailSearch(){
			this.$api.orderCourseDetail({
				courseId:this.id
			}).then(res => {
				this.form = res.data
			})
		},
		detailDel(){
			this.$api.orderCourseDetailDel({
				courseId:this.id
			}).then(res => {
				this.$myLayer.successLayer(res.msg)
			})
		}
	}
}
</script>

<style lang="scss" scoped="scoped">
	.iconfont{
		color: rgb(74, 128, 246);
	}
	.container {
		padding: 0 32px;
		background-color: #F1F3F8;
		min-height: calc(100vh - 60px);
	
		.box {
			background: rgba(255, 255, 255, 1);
			box-shadow: 0px 5px 20px 0px rgba(23, 38, 71, 0.05);
			border-radius: 6px;
			padding: 22px 24px;
			width: 100%;
			position: relative;
			
			>div{
				.course-color{
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					background: #6B96FE;
					padding: 22px 24px;
					
					*{
						color: #FFFFFF;
						display: flex;
					}
					
					div{
						line-height: 36px;
					}
				
					.course-name{
						font-size: 18px;
					}
					
					>div{
						>div{
							&:nth-child(1){
								width: 60px;
								margin-right: 10px;
							}
						}
					}
				}
				
				div:not(.course-color){
					display: flex;
					line-height: 36px;
				}
				
				>div{
					margin-bottom: 20px;
					
					>div{
						&:nth-child(1){
							width: 60px;
							margin-right: 10px;
						}
					}
				}
			}
			
			.flex-between{
				align-items: center;
				margin-bottom: 30px;
				
				ul{
					li{
						align-items: center;
						margin-right: 24px;
						.state-lump{
							width: 16px;
							height: 16px;
							border-radius: 4px;
							margin-right: 12px;
						}
						
						.type-lump{
							width: 24px;
							height: 16px;
							border-radius: 4px;
							margin-right: 12px;
						}
					}
				}
			}
		}
		
		.course-title{
			padding-top: 20px;
			margin-bottom: 24px;
			
			div{
				&:first-child{
					color: #55595C;
					font-weight: bold;
				}
			}
		}
	}
</style>
